.section-value {
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  height: auto;
  padding-top: 30px;
  padding-bottom: 30px;
  max-width: 1920px;
  margin: 0 auto;

  @include laptop {
    padding-left: 15%;
    padding-top: 60px;
    padding-bottom: 60px;
  }
  @include desktop {
    padding-top: 120px;
    padding-bottom: 140px;
  }
}

.section-value-text h2 {
  font-size: 18px;
  background-color: $color-primary;
  color: $color-white;
  margin-bottom: 0;
  padding: 15px;

  @include laptop {
    font-size: 30px;
  }

  @include desktop {
    font-size: 48px;
  }

  &:first-child {
    padding-bottom: 5px;
  }

  &:last-child {
    padding-bottom: 20px;
    line-height: 0.5;
  }
}

.section-value-text h2:first-child {
  padding-top: 10px;
  @include laptop {
    width: 69%;
  }
}

.section-value-text h2:nth-child(2) {
  padding-bottom: 10px;
  padding-top: 0;

  @include laptop {
    max-width: 47%;
    margin-bottom: 25px;
  }
}

.section-value-text p {
  color: $color-white;
  font-size: 14px !important;
  line-height: 22px;
  padding: 11px;
  margin-bottom: 24px;

  @include laptop {
    line-height: 25px;
    padding: 0;
    width: 60%;
  }
}

.section-value-text {
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  @include tablet {
    width: 40%;
  }
}

.section-value-text p:nth-child(2) {
  font-size: 18px;
}
