.section-details .container {
  max-width: 1400px !important;
  width: 100%;
}

.section-details__items {
  display: flex;
  flex-direction: column;
  gap: 80px;
  padding-top: 80px;
  padding-bottom: 80px;
}

.section-details__item {
  display: flex;
  flex-direction: column;
  align-items: center;

  @include tablet {
    justify-content: space-between;
  }
  @include laptop {
    flex-direction: row;
  }
}

.item-reverse {
  @include laptop {
    flex-direction: row-reverse;
  }

  .section-details__item-text {
    max-width: 500px;
  }
}

.section-details__item img {
  object-fit: contain;
  margin-bottom: 40px;

  @include laptop {
    max-width: 50%;
    margin-right: 120px;
    margin-bottom: 0;
  }

  @include desktop {
    max-width: 623px;
  }
}

.section-details__item-text {
  margin-right: auto;

  @include desktop {
    max-width: 450px;
  }
}

.section-details__item-text h2 {
  color: $color-primary;
  @include laptop {
    font-size: 50px;
    margin-bottom: 16px;
  }

  @include desktop {
    margin-bottom: 32px;
  }
}

.section-details__item-text h6 {
  font-size: 22px !important;
  color: $color-dark-text;

  @include laptop {
    margin-bottom: 10px;
  }

  @include desktop {
    margin-bottom: 20px;
  }
}

.section-details__item-text p {
  color: $color-dark-text;
  margin-bottom: 24px;
  font-size: 16px !important;
  line-height: 1.7 !important;

  &:last-of-type {
    margin-bottom: 0;
  }
}

.item-reverse img {
  @include laptop {
    margin-right: 0 !important;
  }
}

.section-details__campaigns {
  margin: 0 auto;
  padding-bottom: 60px;

  @include desktop {
    max-width: 1400px;
  }
}

.services-page .section-details__campaigns h3 {
  color: $color-dark-text;
  font-size: 28px;
  font-weight: 900;
  letter-spacing: -0.1px;
}

.section-details__campaigns-card {
  position: relative;
  background-size: cover;
  background-repeat: no-repeat;
  padding: 14px 20px;
  min-height: 200px;
  display: block;
  width: 100%;

  @include tablet {
    width: 50%;
    min-height: auto;
    height: 140px;
  }
  @include desktop {
    min-height: 227px;
    padding: 30px 45px 12px;
  }
}

.section-details__campaigns-card img {
  position: absolute;
  right: 20px;
  bottom: 20px;
  width: 150px;
}

.section-details__campaigns-cards {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding: 0;
  align-items: center;
  gap: 15px;

  @include tablet {
    justify-content: space-between;
    flex-direction: row;
  }
}

.section-details__campaigns .section-details__campaigns-card h6 {
  font-size: 13px;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: $color-white;
  opacity: 1;
  margin-bottom: 10px;
}

.section-details__campaigns .section-details__campaigns-card h5 {
  text-transform: uppercase;
  color: $color-white;
  opacity: 1;
  letter-spacing: 2px;
  margin-bottom: 5px;
}

.section-details__campaigns .section-details__campaigns-card p {
  font-size: 12px;
  color: $color-white;
  opacity: 1;
  letter-spacing: 1.3px;
  max-width: 190px;
  line-height: 1.6;
}

.section-details__campaigns .section-details__campaigns-card h6::after {
  content: "";
  width: 33px;
  display: block;
  height: 2px;
  background-color: $color-primary;
  margin-top: 7px;
  opacity: 0.5;
}
