.section-video__inner {
  position: relative;
  height: 80dvh;

  @include laptop {
    height: 50dvh;
  }

  @include desktop {
    height: 864px;
  }
}

.section-video__inner-title {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  opacity: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  transition-duration: 0.5s;
  transition-property: opacity;

  @include laptop {
    left: 280px;
  }

  &.is-active {
    opacity: 1;
  }
}

.section-video__inner-title h1 {
  margin-bottom: 0 !important;
}

.section-video .section-video__inner-title h1:first-child {
  font-size: 22px;

  @include laptop {
    font-size: 50px;
    padding: 12px 12px 6px 12px;
  }

  @include desktop {
    font-size: 58px;
  }
}

.section-video .section-video__inner-title h1:nth-child(2) {
  font-size: 22px;

  @include laptop {
    padding: 0px 12px 6px 12px;
    font-size: 50px;
  }
}

.section-video .section-video__inner-title h1:last-child {
  padding: 5px 12px 5px 12px;
  font-size: 22px;

  @include laptop {
    font-size: 50px;
  }

  @include desktop {
  }
}

.section-video .section-video__inner-video {
  width: 100%;
  height: 100%;

  iframe {
    height: 100%;
  }
}

.section-video__inner-title .text-dark {
  font-size: 50px;
}

.section-video__inner-button {
  text-transform: uppercase;
  color: $color-white;
  display: block;
  position: absolute;
  bottom: 20px;
  left: 0;
  right: 0;
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  letter-spacing: 0px;
  font-size: 12px;
  max-width: 200px;
  margin: 0 auto;

  @include laptop {
    font-size: 18px;
  }

  span {
    @include laptop {
      margin-bottom: 20px;
    }
  }

  svg {
    width: 20px;
    height: 20px;

    @include laptop {
      width: 30px;
      height: 30px;
    }
  }
}
